.Gif{

    border: 1px solid #eee;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 30px;
    color: #aaa;
    margin: 16px;
    width: 100%;
}

.Gif img{
    object-fit: contain;

}

i {
    font-size: 80px;
    font-family: monospace;
}